<template>
  <div ref="parent">
    <div class="x-footer">
      <div>
        <div style="display: flex; box-sizing: border-box">
          <div class="duration">{{ format(curPosition) }}</div>
          <div class="progresswrap" style="flex-grow: 1">
            <div class="progress">
              <div class="progress_bg">
                <div
                  class="video_progress"
                  :style="{ width: left + 'px' }"
                ></div>
              </div>
              <div class="progress_btn" :style="{ left: left + 'px' }"></div>
            </div>
          </div>
          <div class="duration">{{ format(duration) }}</div>
        </div>
        <div style="display: flex">
          <div class="x-meida">
            <div class="x-meida-img">
              <img :src="curAid && curAid.coverUrl" />
            </div>
            <div class="x-media-name">
              <h3>{{ curAid && curAid.title }}</h3>
              <h5></h5>
            </div>
          </div>
          <div class="x-media-btn">
            <img
              @click="togglePlay"
              :src="!isPlaying ? footer.startIcon : footer.stopIcon"
            />
          </div>
          <div class="x-media-menu"></div>
        </div>

        <div class="ctrl" style="display: block; text-align: left; margin: 5px">
          <span class="bt" @click="prev">上一个</span>
          <span class="bt" @click="sendEvent('input keyevent 24')">音量+</span>
          <span class="bt" @click="sendEvent('input keyevent 25')">音量-</span>
          <span class="bt" style="float: right" @click="next">下一个</span>
          <span class="bt" style="float: right" @click="load">加载</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
window.$ = $;
export default {
  props: ["curAid"],
  data() {
    return {
      isStore: false,
      left: 0,
      footer: {
        startIcon: "../static/img/start.svg",
        stopIcon: "../static/img/stop.svg",
      },
    };
  },
  mounted: function () {
    this.initProgressBar();
    setTimeout(() => {
      $("#app").css("margin-bottom", $(".x-footer").outerHeight() + 10);
    }, 3000);
  },
  computed: {
    isPlaying() {
      return this.$store.state.isPlaying;
    },
    duration() {
      return this.$store.state.duration;
    },
    curPosition() {
      return this.$store.state.curPosition;
    },
  },
  methods: {
    next() {
      this.$emit("next");
    },
    load() {
      fetch("/api/reLoadPlayList", {
        method: "get",
      }).then((data) => {});
    },
    prev() {
      this.$emit("prev");
    },
    togglePlay() {
      this.$emit(
        "updateProgress",
        this.curPosition / this.duration,
        !this.isPlaying
      );
    },
    sendEvent(str) {
      fetch("/api/event", {
        method: "post",
        body: JSON.stringify({ event: str }),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    format(timeMs) {
      let totalSeconds = timeMs / 1000;

      let seconds = Math.trunc(totalSeconds % 60);
      let minutes = Math.trunc((totalSeconds / 60) % 60);
      let hours = Math.trunc(totalSeconds / 3600);

      if (hours > 0) {
        return `${hours}:${minutes}:${seconds}`;
      } else {
        return `${minutes}:${seconds}`;
      }
    },
    initProgressBar() {
      this.left = ($(".progress").width() * this.curPosition) / this.duration;
      var my = this;
      var left = this.left;

      $(function () {
        var tag = false,
          ox = 0,
          bgleft = 0;
        var progresswidth = $(".progress").width();
        $(".progress_btn").mousedown(function (e) {
          ox = e.pageX - left;
          console.log(ox);
          tag = true;
        });
        $(".progress").mouseup(function () {
          tag = false;
          setTimeout(
            () =>
              my.$emit("updateProgress", my.left / progresswidth, my.isPlaying),
            10
          );
        });

        $(".progress").mousemove((e) => {
          //鼠标移动
          if (tag) {
            left = e.pageX - ox;
            if (left <= 0) {
              left = 0;
            } else if (left > progresswidth) {
              left = progresswidth;
            }
            console.log(left);
            my.left = left;
            // $(".progress_btn").css("left", left);

            //my.curProgress = (this.duration * left) / progresswidth;
            $(".text").html(parseInt((left / progresswidth) * 100) + "%");
          }
        });
        $(".progress_bg").click(function (e) {
          //鼠标点击
          if (!tag) {
            bgleft = $(".progress_bg").offset().left;
            left = e.pageX - bgleft;
            console.log(left);
            if (left <= 0) {
              left = 0;
            } else if (left > progresswidth) {
              left = progresswidth;
            }
            my.left = left;

            $(".text").html(parseInt((left / progresswidth) * 100) + "%");
            //my.curProgress = (this.duration * left) / progresswidth;
          }
        });
      });
    },
  },
  watch: {
    curPosition() {
      this.left = ($(".progress").width() * this.curPosition) / this.duration;
    },
  },
};
</script>

