<template>
  <div>
    <div v-if="rightCn - wrongCn > 0" style="text-align: left; color: red">
      <font-awesome-icon
        v-for="i in rightCn - wrongCn"
        :key="i"
        icon="star"
        fixed-width
      />
      {{ rightCn - wrongCn }}
    </div>

    <ul class="figure-list">
      <li v-for="(item, k) in list" :key="k">
        <div class="itemText">
          <span>{{ item.cnText }}</span
          ><span>-</span><span>{{ item.enText }}</span>
        </div>
        <figure
          class="animated"
          style="cursor: pointer"
          :class="{ tada: clickIndex == k }"
          @click="
            select(k, item);
            clickIndex = k;
          "
          :style="{
            backgroundImage: `url(/api2/proxy?url=${encodeURIComponent(
              item.imgUrl
            )}&enText=${encodeURIComponent(item.enText.trim())})`,
          }"
        ></figure>
        <img
          class="animated"
          v-if="false"
          :src="`/api2/proxy?url=${encodeURIComponent(
            item.imgUrl
          )}&enText=${encodeURIComponent(item.enText.trim())}`"
        />
        <div
          v-if="item.sound"
          class="aDSsMd XniWcz aDSsMd-open"
          style="display: block"
        ></div>
      </li>
    </ul>
    <div style="cursor: pointer">
      <span @click="replay">
        <font-awesome-icon
          :class="{ speaking: speaking }"
          icon="volume-up"
          fixed-width
        />Replay 重播
      </span>
      <span @click="randList()">
        <font-awesome-icon
          :class="{ speaking: speaking }"
          icon="step-forward"
          fixed-width
        />下一个</span
      >
      <audio ref="audio" v-show="false" style="height: 0px; width: 0px"></audio>
    </div>
    <div v-if="showGameIf">
      <GameList id="gamefr" />
      <div
        style="
          position: fixed;
          top: 0;
          cursor: pointer;
          right: 0;
          z-index: 10000;
        "
      >
        <font-awesome-icon
          icon="times-circle"
          fixed-width
          @click="closeGame()"
        />
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
window.$ = $;
import GameList from "./GameList";

export default {
  data: () => {
    return {
      rightCn: 0,
      wrongCn: 0,
      curAct: 0,
      list: [],
      curIndex: 0,
      speaking: false,
      seconds: 15,
      timer: 0,
      clickIndex: -1,
      showGameIf: false,
    };
  },
  components: { GameList },
  mounted: function () {
    //this.randList();
  },
  activated() {
    this.randList();
  },

  deactivated() {
    clearInterval(this.timer);
  },
  computed: {
    listenChangeCn() {
      const { rightCn, wrongCn } = this;
      return { rightCn, wrongCn };
    },
  },
  methods: {
    sendFeedBack(selectIndex) {
      var item = this.list[this.curIndex];
      if (item.rTimes == null) item.rTimes = 0;
      if (selectIndex != this.curIndex) {
        if (item.rTimes > 0) item.rTimes = 0;
        item.rTimes--;
      } else {
        item.rTimes++;
      }

      $.ajax({
        type: "post",
        url: "/api2/save",
        contentType: "application/json",
        data: JSON.stringify(item),
        success: function (d) {},
      });
    },
    closeGame() {
      this.showGameIf = false;
      clearTimeout(this.timer);
      this.randList();
    },
    startShowGame() {
      this.rightCn = 0;
      this.wrongCn = 0;

      this.showGameIf = true;
      this.timer = setTimeout(() => {
        this.showGameIf = false;
        this.randList();
      }, 5 * 60 * 1000);
    },
    replay() {
      var item = this.list[this.curIndex];
      this.say(item.enText);
    },
    getIndicator(who) {
      return "";
    },
    select(index, item) {
      (async () => {
        this.sendFeedBack(index);
        if (index == this.curIndex) {
          this.rightCn++;
          this.curAct = 1;
          await this.say(item.enText, "en")
            .then(() => this.say(item.cnText, "zh"))

            .then(() => this.say(this.getRightSetence(), "en"))
            .then(() => this.loadSound(item));
          if (this.rightCn - this.wrongCn >= 20) {
            return this.startShowGame();
          } else await this.randList();
        } else {
          await this.say(item.enText, "en")
            .then(() => this.say(item.cnText, "zh"))
            //.then(() => this.loadSound(item))
            .then(() => this.say(this.getWrongSetence(), "en"));
          this.wrongCn++;
          this.curAct = -1;
        }
      })();
    },

    randList() {
      let _this = this;
      this.$refs.audio.src = null;
      return $.getJSON("/api2/randList", { n: 3 }).then((data) => {
        _this.list.length = 0;
        _this.list.push(...data);
        _this.curIndex = parseInt(Math.random() * 3);
        _this.replay();
        _this.clickIndex = -1;
      });
    },
    say(str, whovoice = "en") {
      this.speaking = true;
      let _this = this;
      if (whovoice == "en") whovoice = " -v Samantha ";
      else if (whovoice == "zh") whovoice = " -v Ting-Ting ";
      let usingLocal = true;
      if (usingLocal)
        return $.get("/api2/say", { str: whovoice + str }).then(() => {
          this.speaking = false;
        });
      else
        return $.get("/api2/tts", { lan: "en", str: str }).then((data) => {
          return new Promise((resolve, reject) => {
            if (data.URL) {
              _this.$refs.audio.src = data.URL;

              _this.$refs.audio.addEventListener("ended", function () {
                _this.speaking = false;

                return resolve();
              });
              _this.$refs.audio.play();
            }
          });
        });
    },
    getWrongSetence() {
      let list = ["no no no", "oh sorry.", "incorrect", "come on."];
      let i = parseInt(Math.random() * list.length);
      return list[i];
    },
    getRightSetence() {
      let list = ["yes yes yes", "well done", "good job"];
      let i = parseInt(Math.random() * list.length);
      return list[i];
    },
    loadSound(item) {
      return new Promise((resolve, reject) => {
        let bool = true;
        if (bool) return resolve();
        if (item.sound) {
          this.$refs.audio.src = item.sound;
          let timer = setTimeout(resolve, 10000);

          this.$refs.audio.addEventListener("ended", function () {
            resolve();
            this.speaking = false;
            clearTimeout(timer);
          });
          this.$refs.audio.play();
        } else resolve();
      });
    },
  },
  watch: {},
};
</script>

