<template>
  <div>
    <div v-for="(items, cat) in catItems" :key="cat">
      <div style="text-align: left">类别:{{ cat }}</div>
      <ul class="figure-list">
        <li v-for="(item, k) in items" :key="k">
          <div style="text-align: left">
            <span>{{ item.cnText }}</span
            ><span
              style="cursor: pointer"
              @click="deleteIt(catItems, cat, item)"
              >x</span
            >
          </div>
          <figure
            style="cursor: pointer"
            @click="playfrom(item)"
            :style="{
              backgroundImage: `url(/api/proxy?url=${encodeURIComponent(
                item.imgUrl
              )})`,
            }"
          ></figure>
        </li>
      </ul>
    </div>

    <pagination v-model="page" :records="total" @paginate="loadList" />
  </div>
</template>

<script>
import $ from "jquery";
window.$ = $;
import Pagination from "vue-pagination-2";

export default {
  data: () => {
    return {
      curTabIndex: 0,
      isEdit: false,
      showAll_k: false,
      isPreview: false,
      aIndex: 0,
      bIndex: 0,
      catItems: {},
      page: 1,
      total: 0,
      onPullDown: true,
    };
  },
  components: { Pagination },
  computed: {},
  mounted: function () {
    this.loadList();
    //window.addEventListener("scroll", this.scroll, false);
  },

  methods: {
    deleteIt(catItems, cat, item) {
      $.post("/api/delRes", { id: item.id, typeId: item.typeId });
      catItems[cat].splice(catItems[cat].indexOf(item), 1);
      this.$forceUpdate();
    },
    playfrom(item) {
      $.get("/api/playres", { id: item.id, typeId: item.typeId });
    },
    loadList() {
      let _this = this;
      $.getJSON("/api/manRes", { page: this.page, typeId: 2 }).then((data) => {
        _this.catItems = {};
        _this.total = data.total;
        for (let i of data.datas) {
          if (!_this.catItems[i.cat]) {
            _this.catItems[i.cat] = [];
            //_this.$set("catItems", i.cat, []);
          }

          _this.catItems[i.cat].push(i);
        }
        _this.$forceUpdate();
        console.log(_this.catItems);
      });
    },
  },
  watch: {},
};
</script>

