<template>
  <div class="hello">
    <div style="clear: both">
      <ul>
        <li
          class="it"
          :class="{ showAll_k: showAll_k == k }"
          v-for="(it, k) in items"
          :key="it.aid"
        >
          <img
            class="coverUrl"
            :src="it.coverUrl"
            referrerPolicy="no-referrer"
            @click="showAll_k = showAll_k == k ? -1 : k"
          />
          <div>
            <div>
              <b>#{{ k }}</b
              >{{ it.title || it.aid }}:{{ it.aid }}:({{ it.items.length }})
              <span style="cursor: pointer" @click="del(k, -1)">x</span>
            </div>
            <div>
              <li v-for="(item, i) in it.items" :key="item.path">
                <span
                  :class="{ cur: aIndex == k && bIndex == i }"
                  @click="play(k, i)"
                  class="url"
                  :title="item.title"
                  >{{ it.title && it.title != "dy" ? i + 1 : item.title }}</span
                >
                <em v-if="isPreview" class="url" @click="openUrl(k, i)">*</em>
                <em v-if="isEdit" class="url" @click="del(k, i)">x</em>
              </li>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <player
      @updateProgress="updateProgress"
      :curAid="items[aIndex]"
      @next="next"
      @prev="prev"
    ></player>
  </div>
</template>

<script>
import Player from "./Player";

export default {
  components: { Player },
  name: "Main",
  data: () => {
    return {
      curTabIndex: 0,
      tabs: [
        { name: "视频", comp: "Video" },
        { name: "按图识字", comp: "Image" },
        { name: "情景学习", comp: "byWhen" },
      ],
      aIndex: 0,
      bIndex: 0,
      isPlaying: false,
      curPosition: 0,
      percent: 0,
      duration: 0,
      items: [],
      progress: 0,
      updateRemote: 0,
      showAll_k: -1,
      isEdit: false,
      isPreview: false,
    };
  },
  computed: {},
  mounted: function () {
    this.getDatas();

    setInterval(() => {
      this.getStatus();
    }, 2000);
  },
  props: {
    msg: String,
  },
  methods: {
    updateProgress(percent, isPlay) {
      this.curPosition = percent * this.duration;
      this.isPlaying = isPlay;
      this.updateRemote++;
    },
    openUrl(aIndex, bindex) {
      open("/api/url?aIndex=" + aIndex + "&bIndex=" + bindex);
    },
    del(aIndex, bindex) {
      confirm("delete it?") &&
        fetch("/api/delete?aIndex=" + aIndex + "&bIndex=" + bindex, {
          method: "GET",
        }).then((res) => {
          this.getDatas();
        });
    },
    base64(item) {
      let n = "data:image/png;base64," + item.thumb;
      console.log(n);
      return n;
    },
    sendEvent(str) {
      fetch("/api/event", {
        method: "post",
        body: JSON.stringify({ event: str }),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    prev() {
      if (this.items.length == 0) return;
      if (this.bIndex == 0) {
        if (this.aIndex == 0) this.aIndex = this.items.length - 1;
        else this.aIndex--;
        this.bindex = this.items[this.aIndex].length - 1;
      } else this.bIndex--;
      this.updateRemote++;
    },
    next() {
      if (this.items.length == 0) return;
      if (this.bIndex >= this.items[this.aIndex].length - 1) {
        if (this.aIndex >= this.items.length - 1) this.aIndex = 0;
        else this.aIndex++;
        this.bindex = 0;
      } else this.bIndex++;
      this.updateRemote++;
    },
    play(k, i, progressPercent = 0, isPlay = true) {
      fetch("/api/play", {
        method: "post",
        body: JSON.stringify({
          aIndex: k,
          bIndex: i,
          progress: progressPercent,
          isPlaying: isPlay,
        }),
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },

    getDatas() {
      fetch("/api/list.json", {
        method: "GET",
        mode: "cors",
        credentials: "include",
      })
        .then((response) => {
          response.json().then((data) => {
            this.items.length = 0;
            this.items.push(...data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },

    getStatus() {
      fetch("/api/status", {
        method: "GET",
      })
        .then((response) => {
          response.json().then((data) => {
            Object.assign(this, data);
            this.$store.commit("updateStatus", data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },
  },
  watch: {
    updateRemote: {
      handler: function () {
        this.play(
          this.aIndex,
          this.bIndex,
          this.curPosition / this.duration,
          this.isPlaying
        );
      },
      deep: true,
    },
  },
};
</script>

